import React, { useState } from 'react';
import { Mail, Send, AlertCircle, CheckCircle, Loader2 } from 'lucide-react';

export default function BulkEmailSender() {
  const [recipients, setRecipients] = useState('');
  const [subject, setSubject] = useState('');
  const [message, setMessage] = useState('');
  const [sending, setSending] = useState(false);
  const [result, setResult] = useState(null);

  const parseRecipients = (text) => {
    // Split by common delimiters: comma, semicolon, newline, space
    const emails = text
      .split(/[,;\n\s]+/)
      .map(email => email.trim())
      .filter(email => email !== '');
    return emails;
  };

  const getRecipientCount = () => {
    const emails = parseRecipients(recipients);
    return emails.length;
  };

  const handleSend = async () => {
    const validRecipients = parseRecipients(recipients);
    
    if (validRecipients.length === 0) {
      setResult({ success: false, message: 'Please add at least one recipient' });
      return;
    }

    if (!subject.trim()) {
      setResult({ success: false, message: 'Please add a subject' });
      return;
    }

    if (!message.trim()) {
      setResult({ success: false, message: 'Please add a message' });
      return;
    }

    setSending(true);
    setResult(null);

    try {
      const response = await fetch('http://localhost:5000/api/send-bulk-email', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          recipients: validRecipients,
          subject,
          message,
        }),
      });

      const data = await response.json();

      if (response.ok) {
        setResult({ 
          success: true, 
          message: `Successfully sent ${data.successful} email(s). ${data.failed > 0 ? `Failed: ${data.failed}` : ''}` 
        });
        // Reset form
        setRecipients('');
        setSubject('');
        setMessage('');
      } else {
        setResult({ success: false, message: data.message || 'Failed to send emails' });
      }
    } catch (error) {
      setResult({ success: false, message: 'Error connecting to server. Make sure backend is running on port 5000.' });
    } finally {
      setSending(false);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100 p-6">
      <div className="max-w-4xl mx-auto">
        <div className="bg-white rounded-2xl shadow-xl overflow-hidden">
          {/* Header */}
          <div className="bg-gradient-to-r from-blue-600 to-indigo-600 p-6">
            <div className="flex items-center gap-3">
              <Mail className="w-8 h-8 text-white" />
              <h1 className="text-3xl font-bold text-white">Bulk Email Sender</h1>
            </div>
            <p className="text-blue-100 mt-2">Send emails to multiple recipients at once</p>
          </div>

          <div className="p-6 space-y-6">
            {/* Recipients Section */}
            <div>
              <div className="flex items-center justify-between mb-3">
                <label className="text-lg font-semibold text-gray-700">
                  Recipients {getRecipientCount() > 0 && (
                    <span className="text-sm font-normal text-blue-600">
                      ({getRecipientCount()} email{getRecipientCount() !== 1 ? 's' : ''})
                    </span>
                  )}
                </label>
              </div>
              <textarea
                value={recipients}
                onChange={(e) => setRecipients(e.target.value)}
                placeholder="Paste email addresses here. Separate by comma, semicolon, space, or new line.&#10;&#10;Example:&#10;john@example.com, jane@example.com&#10;bob@example.com; alice@example.com&#10;mike@example.com"
                rows={8}
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent resize-none font-mono text-sm"
              />
              <p className="mt-2 text-sm text-gray-500">
                ðŸ’¡ Tip: You can paste emails from Excel, CSV, or any text format. The app will automatically detect and separate them.
              </p>
            </div>

            {/* Subject */}
            <div>
              <label className="text-lg font-semibold text-gray-700 mb-3 block">Subject</label>
              <input
                type="text"
                value={subject}
                onChange={(e) => setSubject(e.target.value)}
                placeholder="Email subject"
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
            </div>

            {/* Message */}
            <div>
              <label className="text-lg font-semibold text-gray-700 mb-3 block">Message</label>
              <textarea
                value={message}
                onChange={(e) => setMessage(e.target.value)}
                placeholder="Type your message here..."
                rows={8}
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent resize-none"
              />
            </div>

            {/* Result Message */}
            {result && (
              <div className={`flex items-start gap-3 p-4 rounded-lg ${
                result.success ? 'bg-green-50 text-green-800' : 'bg-red-50 text-red-800'
              }`}>
                {result.success ? (
                  <CheckCircle className="w-5 h-5 mt-0.5 flex-shrink-0" />
                ) : (
                  <AlertCircle className="w-5 h-5 mt-0.5 flex-shrink-0" />
                )}
                <p className="text-sm">{result.message}</p>
              </div>
            )}

            {/* Send Button */}
            <button
              onClick={handleSend}
              disabled={sending}
              className={`w-full flex items-center justify-center gap-3 px-6 py-4 rounded-lg font-semibold text-white transition-all ${
                sending
                  ? 'bg-gray-400 cursor-not-allowed'
                  : 'bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-700 hover:to-indigo-700 shadow-lg hover:shadow-xl'
              }`}
            >
              {sending ? (
                <>
                  <Loader2 className="w-5 h-5 animate-spin" />
                  Sending...
                </>
              ) : (
                <>
                  <Send className="w-5 h-5" />
                  Send Bulk Email
                </>
              )}
            </button>
          </div>
        </div>

        {/* Instructions */}
        <div className="mt-6 bg-white rounded-lg shadow p-6">
          <h2 className="text-xl font-semibold text-gray-800 mb-3">Backend Setup Instructions</h2>
          <ol className="list-decimal list-inside space-y-2 text-gray-600">
            <li>Create a new folder for your backend and run: <code className="bg-gray-100 px-2 py-1 rounded">npm init -y</code></li>
            <li>Install dependencies: <code className="bg-gray-100 px-2 py-1 rounded">npm install express cors nodemailer dotenv</code></li>
            <li>Create the server.js file (see artifact below)</li>
            <li>Create a .env file with your email credentials</li>
            <li>Run the server: <code className="bg-gray-100 px-2 py-1 rounded">node server.js</code></li>
          </ol>
        </div>
      </div>
    </div>
  );
}